body {
  font-family: 'Roboto', sans-serif
}
.layout > header{
	border-bottom: solid 3px #EE8601;
    /*margin-bottom: 15px*/
}

.layout > header .navbar-brand img{
    max-width: 140px;
}

@media (min-width: 576px) {
    .layout > header .navbar-brand img{
        max-width: none
    }

    .layout > header{
        margin-bottom: 30px;
    }
}

.layout > header .navbar{
	/*padding-left: 0*/
}

.layout > header .navbar-brand{
    margin: 0 auto;
}

.layout > header .navbar-toggler{
	padding: 0;
	border: 0;
    margin-left: -30px;
    outline: none;
}

.navbar-light .navbar-toggler-icon{
    background-image: url("data:image/svg+xml;base64,");
}

.layout .sidebar-left {
    padding-top: 15px
}

.layout .sidebar-left h5 {
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 500;
    color: #383B3E;
    font-family: 'Roboto', sans-serif;
}

.layout .sidebar-left .nav {
    display: -webkit-inline-flex;
    display: inline-flex;
}

.layout .sidebar-left a {
    color: #383B3E;
    padding: .5rem 0;
}

.layout .nav-pills .nav-link.active {
    background-color: transparent;
    color: #383B3E;
    font-weight: 600;
    border-bottom: solid 2px #00AEEF;
    border-radius: 0;
    width: auto;
    display: inline-block;
}

.distrito-header{
	padding: 15px;
	background-color: rgba(233,233,235,0.4);
	border-radius: 6px;
	margin-bottom: 10px
}

.distrito-header h5{
	text-transform: uppercase;
	font-size: 16px;
	font-weight: 900;
	color: #383B3E;
	font-family: 'Montserrat', sans-serif;
}

.distrito-header p{
	font-size: 12px;
	color: #6E7276;
}

.distrito-header .btn-primary{
	background-color: #00AEEF;
	font-size: 12px;
	text-transform: uppercase;
	height: auto;
	border: 0;
	padding: 5px 15px
}

.turno .media{
	border-bottom: solid 1px rgba(216, 216, 216, .7);
	padding: 10px 0px
}

.turno .media h5{
	font-size: 16px;
	color: #383B3E;
}

.turno:hover {
	text-decoration: none;
}

.turno .media .ticket{
	background-color: #00AEEF;
	border: solid 2px #00AEEF;
	border-radius: 6px;
	min-width: 80px;
	text-align: center;
}

.turno .media .ticket span{
	display: block;
}

.turno .media .ticket span.turno{
	padding: 0 5px;
	color: #383B3E;
	background-color: #FFF;
	font-size: 26px;
	font-weight: 600;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
}

.turno .media .ticket span.label{
	color: #FFFFFF;
	font-size: 10px;
	font-weight: 900;
	text-transform: uppercase;
	text-align: center;
}

.turno .media .arrow-icon{
	margin-left: 10px;
	background-image: url("data:image/svg+xml;base64,");
	display: inline-block;
    width: 1.5em;
    height: 1.5em;
    vertical-align: middle;
    content: "";
    background-size: 100% 100%;
}

